import { Polygon as LPolygon} from 'leaflet';
import AbstractPoly from './AbstractPoly';
import * as utils from '../../utils';
import { mapDataModel } from '../../models';

/**
 * 多边形
 * 
 * @export
 * @class Polygon
 * @extends {Layer}
 * 
 * @param {Array.<Array>} pos 描述各个点坐标
 * @param {Object} options 选项
 */
export default class Polygon extends AbstractPoly {
  constructor(pos, options) {
    super(pos, options);
  }
  /**
   * 创建多边形
   * 
   * @override 
   * @param {Leaflet.Map} map 
   * @return {Leaflet.Polygon}
   * 
   * @memberof Polygon#
   */
  createGraphics() {
    const latlng = utils.upprojectList(this.pos, mapDataModel.CRS); 
    return new LPolygon(latlng, this.options);
  }
}


