/**
 * 绘制的始末
 * 
 * @private 
 * @module action/draw
 */

import { DomEvent } from 'leaflet';

let map = null;

function initialize(leafletMap) {
  map = leafletMap;
  if (checkEditable()) {
    map.editTools.addEventListener('editable:drawing:commit', featureAddedHandler);
  }
}
/**
 * 画线
 */
function startPolyline(styles = null) {
  if (checkEditable()) {
    const line = map.editTools.startPolyline();
    if (styles) line.setStyle(styles);
    return line;
  }
}
/**
 * 画多边形
 */
function startPolygon(styles = null) {
  if (checkEditable()) {
    const polygon = map.editTools.startPolygon();
    if (styles) polygon.setStyle(styles);
    return polygon;
  }
}
/**
 * 检查绘制的条件, 这里绘制和edit是依赖关系
 * 
 * @return {Boolean} 
 */
function checkEditable() {
  if (map.options.editable && map.editTools) {
    return true;
  }
}
/**
 * 绘制完某个图形的处理器
 * 
 * @private 
 * @param {Object} event 
 */
function featureAddedHandler(event) {
  const layer = event.layer;
  layer.on('dblclick', toggleEidtHandler);
  layer.on('click', deleteLayerHandler)
}
/**
 * 双击切换编辑状态的处理器
 * 
 * @private
 * @param {Object} event 
 */
function toggleEidtHandler(event) {
  DomEvent.stopPropagation(event);
  event.target.toggleEdit();
}
/**
 * 删除创建图形的处理器
 * 
 * @private 
 * @param {Object} event 
 */
function deleteLayerHandler(event) {
  DomEvent.stopPropagation(event);
  const layer = event.target;
  if ((event.originalEvent.ctrlKey || event.originalEvent.metaKey) && layer.editEnabled()) {
    layer.editor.deleteShapeAt(event.latlng);
  }
}

export default {
  initialize,
  startPolyline,
  startPolygon
}


