import L from 'leaflet';
import { EventDispatcher } from '../events';
import './control.less';

export default class Control extends EventDispatcher {
  constructor(position) {
    super();
    this._position = position;
  }
  /**
   * 获取位置信息, 这里是字符串, 描述是左上, 右下诸如此类的信息
   * 
   * @return {String}
   * 
   * @memberOf Control
   */
  getPosition() {
    return this._position;
  }
  /**
   * 设置位置, 同样针对的是左上 右下这种形式
   * 
   * @param {String} position 
   * @return {Contorl}
   * 
   * @memberOf Control
   */
  setPosition(position) {
    if (this._map) {
      this._map.removeControl(this);
    }
    this._position = position;

    if (this._map) {
      this._map.addControl(this);
    }
    return this;
  }
  /**
   * 添加, 应该说是添加到position决定的特定的容器
   * 
   * @param {leafletMap} map 
   * @return {Control}
   * 
   * @memberOf Control
   */
  addTo(map) {
    this.remove();
    this._map = map;
    const pos = this.getPosition();
    const corner = map._controlCorners[pos];
    this._container = this.onAdd();
    L.DomUtil.addClass(this._container, 'leaflet-control');
    if (pos.indexOf('bottom') !== -1) {
      corner.insertBefore(this._container, corner.firstChild);
    } else {
      corner.appendChild(this._container);
    }
    if (this.onAdded) this.onAdded(map);
    return this;
  }
  /**
   * 同上, 从容器中直接干掉
   * 
   * @return {Control}
   * 
   * @memberOf Control
   */
  remove() {
    if (!this._map) {
      return this;
    }
    L.DomUtil.remove(this._container);
    if (this.onRemove) {
      this.onRemove(this._map);
    }
    this._map = null;
    return this;
	}
  /**
   * 和原框架保持一致, 在即将添加到容器时调用, 在这里要返回UI
   * 属于一定要实现的方法
   * 
   * @param {leafletMap} map 
   * 
   * @memberOf Control
   */
  onAdd(map) {
     throw new Error('IllegalOperationError Control onAdd');
  }
  /**
   * 删除后的钩子
   * 
   * @param {leafletMap} map 
   * 
   * @memberOf Control
   */
  onRemove(map) {}
  /**
   * 获得自定义容器
   * 
   * @return {HTMLElement}
   * 
   * @memberOf Control
   */
  getContainer() {
		return this._container;
	}
  _refocusOnMap(e) {
    // if map exists and event is not a keyboard event
    if (this._map && e && e.screenX > 0 && e.screenY > 0) {
      this._map.getContainer().focus();
    }
  }
}


