/**
 * 原本的设计是vo, 是一个约定的数据结构. 但是如果仅仅只返回vo, 用户基本做不了啥
 * 从行为上来看, 更像是ui, 能操作一些样式事件等
 * 但是注意, 仅仅是像ui而已, 并没有继承任何Layer或是实现Layer的接口
 */

import { DomUtil } from 'leaflet';
import { EventDispatcher } from '../../events';
import Config from '../../configs';
import * as utils from '../../utils';

export default class Store extends EventDispatcher {
  constructor(region) {
    super();
    const properties = region.feature.properties;
    /**
     * 门店id
     * 
     * @member {Int} id
     * @memberof Store#
     */
    this.id = region.feature.id;
    /**
     * 门店名
     * 
     * @private
     * @member {String} name
     * @memberof Store#
     */
    this._name = properties.re_name;
    /**
     * 门店类型
     */
    this._regionType = properties.re_type;
    /**
     * 门店的矢量图形
     * 
     * @private 
     * @member {Polygeo} feature
     * @memberof Store#
     */
    this._feature = region;
    /**
     * 支持一下选中的逻辑
     * 
     * @private 
     * @member {Boolean} _selected
     */
    this._selected = false;
    //
    this.injectDispatcher(region);
  }
  /**
   * 设置样式
   * 
   * @param {Object} style 
   * 
   * @memberof Store#
   */
  setStyle(style) {
    this._feature.setStyle(style);
  }
  get selected() {
    return this._selected;
  }
  /**
   * set selected
   * 
   * @memberof Store#
   */
  set selected(value) {
    if (this._selected == value)  return;
    this._selected = value;
    if (value) {
      DomUtil.addClass(this._feature.getElement(), 'selected');
    } else {
      DomUtil.removeClass(this._feature.getElement(), 'selected');
    }
  }
  /**
   * 获得Region名
   * 
   * 
   * @memberOf Store#
   */
  get name() {
    return this._name;
  }
  /**
   * 设置Region名
   * 
   * 
   * @memberOf Store#
   */
  set name(value) {
    this._name = value;
    this._feature.label.setContent(value);
  }
  /**
   * 获取门店类型
   * 
   * 
   * @memberOf Store#
   */
  get regionType() {
    return this._regionType;
  }
  /**
   * 设置门店类型
   * 
   * 
   * @memberOf Store#
   */
  set regionType(value) {
    this._regionType = value;
  }
  get feature() {
    return this._feature;
  }
  /**
   * 开始编辑
   * 
   * @return {Editor} 
   * 
   * @memberOf Store#
   */
  enableEdit() {
    this._addDragEvent();
    return this._feature.enableEdit();
  }
  /**
   * 关闭编辑
   * 
   * 
   * @memberOf Store#
   */
  disableEdit() {
    this._removeDragEvent();
    this._feature.disableEdit();
  }
  /**
   * 是否开启编辑
   * 
   * @return {Boolean}
   * 
   * @memberOf Store#
   */
  editEnabled() {
    return this._feature.editEnabled();
  }
  /**
   * 切换编辑状态
   * 
   * 
   * @memberOf Store#
   */
  toggleEdit() {
    this._feature.toggleEdit();
    if (this._feature.editEnabled()) {
      this._addDragEvent();
    } else {
      this._removeDragEvent();
    }
  }
  /**
   * 获取源坐标组
   * 
   * @override 
   * @return {Array.<Array>}
   * 
   * @memberOf AbstractPoly
   */
  getOriginalLatlng() {
    // const list = this._feature.getLatLngs();
    // const result = [];
    // // 特别恶心, 居然里面套这么多层. 
    // for (let i = 0; i < list.length; ++i) {
    //   var innerList = list[i], innerTemp = [];
    //   for (let j = 0; j < innerList.length; ++j) {
    //     var latlngList = innerList[j], temp = [];
    //     latlngList.forEach(latlng => {
    //       const point = utils.toOriginalCoordinates(latlng);
    //       temp.push([point.x, point.y]);
    //     });
    //     innerTemp.push(temp);
    //   }
    //   result.push(innerTemp);
    // }
    // return result;
    return utils.getOriginalByLatlngs(this._feature.getLatLngs());
  }
  /**
   * 主要提供删除feature时, 同时把label也删除的功能
   * 
   * 
   * @memberOf Store#
   */
  remove() {
    if (this._feature) {
      this._feature.remove();
      this._feature.label && this._feature.label.remove();
    }
  }
  _addDragEvent() {
    this._feature.on('editable:drag', this._featureDragHandler);
  }
  _removeDragEvent() {
    this._feature.off('editable:drag', this._featureDragHandler);
  }
  _featureDragHandler(event) {
    const center = event.layer.getCenter();
    event.layer.label.setLatLngs(center);
  }
}


