import { EventDispatcher, EventTypes } from '../events';
import { mapDataService } from '../services';
import { mapDataModel } from '../models';

let main = null;

function initialize(floorControl, map) {
  main = map;
  floorControl.on(EventTypes.FLOOR_CHANGE, floorChangeHandler);
}

function floorChangeHandler(event) {
  mapDataService.getData(mapDataModel.plazaID, event.floor);
}


export {
  initialize
}


