/**
 * 在Layer里调用Popup相关的行为, 放在这里主要是为了精简Layer类的代码行数
 * 这里放弃了包装Popup的思路, 改为包装接口, 这样既调用起来比较顺畅, 也能最大程度利用框架提供的基本能力
 */

import { Popup } from 'leaflet';
import doT from 'dot/doT.js';
import template from './tpl.html';

function bindPopup(content, options, layer) {
  if (!layer.layerDisplay) { return; }
  if (options.className) {
    options.className += ' ffmap-map-popup';
  } else {
    options.className = 'ffmap-map-popup';
  }
  layer.layerDisplay.bindPopup(formatContent(content), options);
}

function formatContent(content) {
  if (content instanceof Popup || typeof content === 'string') {
    return content;
  } 
  if (typeof content === 'object' && content.constructor === Object) {
    const tempComplier = doT.compile(template);
    return tempComplier(content);
  }
  return '';
}

function unbindPopup(layer) {
  if (!layer.layerDisplay) { return; }
  layer.layerDisplay.unbindPopup();
}

function openPopup(layer, latlng) {
  if (!layer.layerDisplay) { return; }
  layer.layerDisplay.openPopup(layer.layerDisplay, latlng);
}

function closePopup(layer) {
  if (!layer.layerDisplay) { return; }
  layer.layerDisplay.closePopup();
}

function togglePopup(layer) {
  if (!layer.layerDisplay) { return; }
  layer.layerDisplay.togglePopup(layer.layerDisplay);
}
function setPopupContent(layer, content) {
  if (!layer.layerDisplay) { return; }
  layer.layerDisplay.setPopupContent(formatContent(content));
}

function isPopupOpen(layer) {
  if (!layer.layerDisplay) { return; }
  return layer.layerDisplay.isPopupOpen();
}

export default {
  bindPopup,
  unbindPopup,
  openPopup,
  closePopup,
  togglePopup,
  setPopupContent,
  isPopupOpen
};


