/**
 * 室内地图的渲染器
 */

import L from 'leaflet';
import AbstractRender from '../AbstractRender';
import * as utils from '../../utils';

'use strict';

const defaultOption = {
  crs: L.CRS.EPSG3395,
  fillOpacity: 0.6,
  interactive: false
};

export default class GeoRender extends AbstractRender {
  /**
   * 渲染地图的某一层
   * 
   * @public
   * @param {Object|Promise} data 如果类型是Object, 那么意味着是一个GeoJson的数据格式 
   * @param {Object} option 
   * @return {Promise}
   * @memberof GeoRender#
   */
  render(data, option) {
    if (!this._map) { throw new ReferenceError('IndoorMapRender this._map is null'); }
    option = Object.assign({}, defaultOption, option);
    option.coordsToLatLng = function(coords) {
      return option.crs.projection.unproject(L.point(coords[0], coords[1]));
    };
    if (data && typeof data.then === 'function') {
      return this._renderPromise(data, option);
    }
    return new Promise((resolve, reject) => {
      resolve(this._render(data, option));
    });
  }
  /**
   * 清除所有图层
   * 
   * @public
   * @memberof GeoRender#
   */
  clear() {
    if (this._layerList && this._layerList.length > 0) {
      this._layerList.forEach(layer => {
        layer.clearLayers();
      });
      this._layerList = [];
    }
  }
  /**
   * 具体绘制并新建layer的方法
   * 
   * @protected 
   * @param {Object} data GeoJson 
   * @param {Object} option 
   * @return {leafletGeoLayer}
   * 
   * @memberof GeoRender#
   */
  _render(data, option) {
    let layer = L.geoJson(data, option).addTo(this._map);
    this._layerList.push(layer);
    return layer;
  }
  /**
   * 对传入Promise进行兼容, 得到数据后实质上还是调用 this._render
   * 
   * @protected 
   * @param {Promise} promise 
   * @param {Object} option 
   * @return {Promise}
   * 
   * @memberof GeoRender#
   */
  _renderPromise(promise, option) {
    return promise.then(data => {
      return this._render(data, option);
    })
  }
}



