import ajax from './ajax';
import { mapDataModel } from '../models';
import { EventDispatcher, EventTypes } from '../events';
import { mapDataUtils } from '../utils';
import GlobalConfig from '../configs';

// const prefixURL = '';

export default class MapDataService extends EventDispatcher {
  getData(plazaID, floor = '') {
    // 读缓存
    if (mapDataModel.getMapData(plazaID, floor)) {
      this.fire(EventTypes.GET_MAP_DATA_SUCCESS, mapDataModel.getMapData(plazaID, floor));
      return;
    }
    // 读接口
    this.fire(EventTypes.GET_MAP_DATA_START, {message: 'start getting data'});
    this._getData(plazaID, floor);
  }
  _getData(plazaID, floor = '') {
    Promise.all(
      [
        ajax().get(`${GlobalConfig.api.mapDataBaseAPI}/floor?plazaId=${plazaID}&floor=${floor}`), 
        ajax().get(`${GlobalConfig.api.mapDataBaseAPI}/region?plazaId=${plazaID}&floor=${floor}`)
      ])
      .then(values => {
        var floorData = values[0];
        var regionData = values[1];
        if (floorData.status == 200 && regionData.status == 200 && floorData.data && regionData.data) {
          this._setDataToModel(plazaID, { floorData: floorData.data, regionData: regionData.data });
          this.fire(EventTypes.GET_MAP_DATA_SUCCESS, { floorData: floorData.data, regionData: regionData.data });
        } else {
          this.fire(EventTypes.GET_MAP_DATA_ERROR, {message: 'parse data error'});
        }
      })
      .catch(err => {
        console.error('get plaza data error!', err);
        this.fire(EventTypes.GET_MAP_DATA_ERROR, {message: 'get plaza data error!'});
      });
  }
  /**
   * 将数据缓存到model
   * 
   * @private
   * @param {string} plazaID 
   * @param {object} data 
   * 
   * @memberOf MapDataService#
   */
  _setDataToModel(plazaID, data) {
    const floor = data.regionData.currentFloor;
    //
    if (!mapDataModel.mapCenterPoint) {
      mapDataModel.mapCenterPoint = mapDataUtils.getMapCenterPoint(data.regionData);
      mapDataModel.mapCenterPoint = mapDataModel.mapCenterPoint ? mapDataModel.mapCenterPoint : [0, 0];
    }
    // 格式化
    mapDataUtils.formatData(data.floorData, mapDataModel.mapCenterPoint);
    mapDataUtils.formatData(data.regionData, mapDataModel.mapCenterPoint);
    //
    mapDataModel.setMapData(plazaID, floor, data);
    mapDataModel.plazaID = plazaID;
    mapDataModel.buildingID = data.floorData.features[0].properties.build_id;
  }
}


