import markerConfig from './commonMarker.json';


function getMarkerConfig(id) {
  id = parseInt(id) + '';
  return markerConfig[id];
}

function hasMarkerConfig(id) {
  id = parseInt(id) + '';
  return markerConfig.hasOwnProperty(id);
}

export default {
  getMarkerConfig,
  hasMarkerConfig
}


