import 'leaflet/dist/leaflet.css';
import './index.less';
import FFanMap from './Map';
import { Marker } from './ui/marker';
import { Label, PoiLabel } from './ui/label';
import { Popup } from './ui/popup';
import { EventTypes } from './events';
import Polyline from './ui/poly/Polyline';
import Polygon from './ui/poly/Polygon';
import Rectangle from './ui/poly/Rectangle';
import { toOriginalCoordinates, latLngToPoint, pointToLatLng, getOriginalByLatlngs } from './utils';
import { Point, LatLng } from 'leaflet';


'use strict';

const FMap = {
  Map: FFanMap,
  EventTypes,
  Marker,
  Label,
  PoiLabel,
  Polyline,
  Polygon,
  Rectangle,
  Popup,
  Utils: {
    toOriginalCoordinates,
    latLngToPoint,
    pointToLatLng,
    getOriginalByLatlngs
  },
  /**
   * @namespace
   */
  
  Geom: {
    /**
     * 来自leaflet的Point
     * 
     * @type {Point}
     * @see http://leafletjs.com/reference-1.0.3.html#point
     */
    Point,
    /**
     * 来自leaflet的Latlng
     * 
     * @type {LatLng}
     * @see http://leafletjs.com/reference-1.0.3.html#latlng
     */
    LatLng
  }
};


export { FMap };
export default FMap;



