/**
 * Render 工厂.
 * 在设计上 分不同的地图渲染模式
 */

import * as RenderTypes from './types';
import IndoorRender from './indoor/IndoorRender';

'use strict';

/**
 * create Render
 * 
 * @export
 * @param {String} type 渲染器类型 
 * @returns {IRender|AbstractRender}
 */
export default function create(type, map) {
  let render = null;
  switch (type) {
    case RenderTypes.INDOOR :
      render = new IndoorRender();
    default :
      render = new IndoorRender(); 
  }
  render.initialize(map);
  return render;
}


