/**
 * 
 * @private 
 * @module  draw/index
 */

import './draw.less';
import DrawControl from './DrawControl';
import 'leaflet.path.drag';
import 'leaflet-editable';

let drawControl = null;

/**
 * 初始化, 无论地图有没有开始编辑功能, 这里先把层级都确定好
 * 
 * @param {LeafletMap} name 地图实例
 */
function initialize(map) {
  const pane = map.createPane('poly');
  const referenceElement = map.getPane('markerPane');
  map.getPane('mapPane').insertBefore(pane, referenceElement);
  const mapOptions = map.options;
  if (mapOptions.editable && mapOptions.drawControl) {
    drawControl = new DrawControl();
    drawControl.addTo(map);
  }
}

export default {
  initialize
}


