const defaultConfig = {
  /**
   * congrol层的位置
   * 
   * @type {String}
   */
  position: 'bottomleft',
  /**
   * 地上楼层的前缀
   * 
   * @type {String}
   */
  prefixFloor: 'F',
  /**
   * 地下楼层的前缀
   * 
   * @type {String}
   */
  prefixBasement: 'B',
  /**
   * 默认选择的楼层, 注意这里是真楼层, 逻辑里会对应到数组里面位置
   * 
   * @tytype {Int}
   */
  defaultFloor: 1
};

export default defaultConfig;

export function merge(config) {
  Object.assign(defaultConfig, config);
}


