/**
 * 封装的一个Popup，传入popup打开的坐标，这种方式在定义的时候不用不关联layer
 */
import './popup.less';
import L, { DomUtil, Point } from 'leaflet';
import { mapDataUtils } from '../../utils';
import Layer from '../Layer';

/**
 * Popup
 * 
 * @export
 * @class Popup
 * @extends {L.Popup}
 * 
 * @param {Array|Object|String} pos 坐标
 * @param {Object} options offset和marker上bindPopup设置的popupAnchor对应，打开高度一致
 * 
 */
export default class Popup extends Layer {
  constructor(options = {}, layer = null) {
    super();
    this._titleContainer = null;
    this._contentContainer = null;
    this._wrapperContainer = null;
    this._options = options;
    this._options.closeOnClick = options.closeOnClick || false;
    this._options.offset = options.offset || (new Point(0, -17));
    this._popup = L.popup(this._options);
    this._setContent();

    if(options.pos) {
      this._popup.setLatLng( mapDataUtils.getLatlng(options.pos) );
    }
    this.injectDispatcher(this._popup);
  }
  /**
   * 系统钩子
   *  
   * @private 
   * @param {LeafletMap} map 
   * 
   * @memberOf Popup#
   */
  onAdd(map) {
    if (this._popup) {
      this._popup.addTo(map);
    }
  }
  /**
   * 系统钩子
   * 
   * @private 
   * @memberOf Popup#
   */
  onRemove() {
    if (this._popup) {
      this._popup.remove();
    }
  }
  /**
   * 设置坐标，并不是必须，在marker上打开的时候不需要设置也行
   * 
   * @param {Array|Object|String} pos 坐标
   * @memberof Popup#
   */
  setLatLng(pos) {
    this._popup.setLatLng(pos);
  }
  /**
   * 设置content
   * 
   * @param {String} content html字符串
   * @memberof Popup#
   */
  setContent(content) {
    if(content && typeof content == 'string') {
      this._contentContainer.innerHTML = content;
    }
  }
  /**
   * 设置默认content，就是图片+描述，至少要传入一张图片
   * 
   * @param {String} content html字符串
   * @memberof Popup#
   */
  setDefaultContent(img, description = '') {
    if(!img) {
      return;
    }

    const _content = DomUtil.create('div', 'ffmap-map-popup-default', this._contentContainer);
    const _img = DomUtil.create('img', 'ffmap-map-popup-img', _content);
    
    _img.src = img;
    
    if(description) {
      const _desc = DomUtil.create('div', 'ffmap-map-popup-description', _content);
      _desc.innerText = description;
    }

  }
  /**
   * 设置title
   * 
   * @param {String} title title字符串
   * @memberof Popup#
   */
  setTitle(title) {
    title = title || '我的位置';
    this._titleContainer.innerHTML = title;
  }
  /**
   * 重写整个content，包括title部分
   * 
   * @param {String|HTMLElement|Function} html html字符串
   * @memberof Popup#
   */
  setWrapper(html) {
    if(html) {
      this._popup.setContent(html);
    }
  }
  /**
   * 设置content，如果有在options传入content，也在这里处理
   * 
   * @private 
   * @memberOf Popup#
   */
  _setContent() {
    this._wrapperContainer = DomUtil.create('div', 'ffmap-map-popup-wrapper');
    this._titleContainer = DomUtil.create('div', 'ffmap-map-popup-title', this._wrapperContainer);
    this._contentContainer = DomUtil.create('div', 'ffmap-map-popup-content', this._wrapperContainer);

    this.setTitle(this._options.title);

    if(this._options.content) {
      this.setContent(this._options.content);
    }
    else if (this._options.img) {
      this.setDefaultContent(this._options.img, this._options.description);
    }

    this.setWrapper(this._wrapperContainer);
  }
  /**
   * 打开Popup
   * 
   * @memberOf Popup#
   */
  open(map) {
    map.addOverlay(this._popup);
  }
  /**
   * 关闭popup
   * 
   * @memberOf Popup#
   */
  close() {
    this._popup._close();
  }
  /**
   * 绑定在marker上
   * 
   * @memberOf Popup#
   */
  bindLayer(layer) {}
}



