/**
 * poi label 实际就是店铺的label, 要随着店铺放大缩小移动位置的
 */

import L, { Util, DomUtil, Point } from 'leaflet';
import Label  from './Label';

export default class PoiLabel extends Label {
  /**
   * 初始化Label
   *
   * @override
   * @param {Array} latlng 和官方实现一样的接口
   * @param {String} label 要显示的文字
   * @param {Object} options 要覆盖的选项
   */
  constructor(pos, label, relevantLayer, options) {
    super(pos, label, options);
    Util.setOptions(this, options);
    this._relevantLayer = relevantLayer;
    // 用来缓存某个级别是否显示
    this._canShowCache = {};
  }
  getEvents() {
    return {
      zoom: this._zoomHandler,
      viewreset: this.updatePos,
      zoomstart: this._zoomStartHandler,
      zoomend: this._zoomEndHandler
    };
  }
  /**
   * 钩子
   * 
   * @param {leafletMap} map 
   * @memberof PoiLabel#
   */
  onAdd(map) {
    super.onAdd(map);
    if (!this._canShow()) {
      this._zoomHide();
    }
    return this._container;
  }
  onRemove(map) {
    this._relevantLayer = null;
    this._canShowCache = null;
    super.onRemove(map);
  }
  /**
   * 根据相关layer更新位置
   * 
   * @override 
   * @memberof PoiLabel#
   */
  updatePos() {
    var pos = this._moveToCenter(this._map.latLngToLayerPoint(this._latlng).round());
		this._setPos(pos);
  }
  /**
   * 为了实现poi专门实现的, 这样就不用控制状态了
   * 
   * @private
   * @memberof PoiLabel
   */
  _zoomShow() {
    DomUtil.removeClass(this._container, 'zoom-hide');
  }
  /**
   * 同上
   * 
   * @private
   * @memberof PoiLabel
   */
  _zoomHide() {
     DomUtil.addClass(this._container, 'zoom-hide');
  }
  /**
   * 获得关联图层(poi)的大小
   *
   * @private
   * @memberof PoiLabel#
   * @return {Object}
   */
  _getPOISize() {
    if (!this._relevantLayer || !this._map) return {width: 0, height: 0};
    const bounds = this._relevantLayer.getBounds();
    const
      sw = this._map.latLngToLayerPoint(bounds.getSouthWest()),
      se = this._map.latLngToLayerPoint(bounds.getSouthEast()),
      ne = this._map.latLngToLayerPoint(bounds.getNorthEast()),
      nw = this._map.latLngToLayerPoint(bounds.getNorthWest());
    const width = Math.max(ne.x, se.x) - Math.max(nw.x, sw.x);
    const height = Math.max(sw.y, se.y) - Math.max(nw.y, ne.y);
    return { width, height };
  }
  /**
   * 移动到poi图形的中间
   * 
   * @private
   * @param {Point} pos 
   * @return {Point}
   * 
   * @memberOf PoiLabel#
   */
  _moveToCenter(pos) {
    const size = this.getSize();
    return pos.add(new Point(-size.width * 0.5, -size.height * 0.5));
  }
  /**
   * 这个隐藏仅仅是个效果
   * 
   * @private
   * @memberOf PoiLabel#
   */
  _zoomStartHandler() {
    DomUtil.addClass(this._container, 'hide');
  }
  _zoomEndHandler() {
    DomUtil.removeClass(this._container, 'hide');
  }
  /**
   * 层级改变时, 检查poi是否够显示label, 并做个缓存
   * 
   * @param {Event} event 
   * @private
   * @memberOf PoiLabel#
   */
  _zoomHandler(event) {
    const zoom = event.target.getZoom();
    let canshow;
    if (this._canShowCache.hasOwnProperty(zoom)) {
      canshow = this._canShowCache[zoom];
    } else {
      canshow = this._canShow();
      this._canShowCache[zoom] = canshow;
    }
    if (canshow) {
      this.updatePos();
      this._zoomShow();
    } else {
      this._zoomHide();
    }
  }
  /**
   * 如果poi太小了, 主要是宽太小了就不显示了
   * 
   * @private
   * @return {Boolean}
   * @memberOf PoiLabel#
   */
  _canShow() {
    const size = this.getSize();
    const poiSize = this._getPOISize();
    return poiSize.width - size.width > 1;
  }
  /**
   * @override 
   * @private
   * @param {Array.<String>} classList 
   * @return {String}
   * @memberOf PoiLabel#
   */
  getLabelClassName(classList) {
    const defaultList = ['ffan-map-label', 'poi-label'];
    return defaultList.concat(classList || []).join(' ');
  }
};


