/**
 * 事件的超类, 这里我们作为一个代理来实现, 默认代理 L.Evented的一个实例
 * 
 * 这里作为基础类, 所有需要有事件功能的类都应该继承此类
 */

import { Evented } from 'leaflet';

'use strict';

export default class EventDispatcher extends Evented {
  /**
   * 注入事件实例, 其实就是注入一个代理, 最常见的做法就是把map注入进来, 那么注册监听的都是map上的事件, 可以理解为全局事件
   * 
   * @protected 
   * @param {IEventDispatcher} dispatcher 
   * @memberof EventDispatcher#
   */
  injectDispatcher(dispatcher) {
    if (dispatcher) {
      this._proxy = dispatcher;
    }
  }
  /**
   * 注册事件
   * 
   * @param {String} type 事件类型
   * @param {Function} handler 处理器
   * @param {Object} [context=null] 上下文
   * @param {Boolean} [propagate=false] 是否传递到父级
   * @return {EventDispatcher}
   * 
   * @memberOf EventDispatcher#
   */
  on(type, handler, context = null, propagate = false) {
    super.on(type, handler, context);
    if (this._proxy) {
      this._proxy.on(type, event => {
        super.fire(type, event, propagate);
      }, this);
    }
    return this;
  }
  /**
   * 注册只触发一次的事件
   * 
   * @param {String} type 事件类型
   * @param {Function} handler 处理器
   * @param {Object} [context=null] 上下文
   * @return {EventDispatcher} 
   * 
   * @memberOf EventDispatcher#
   */
  once(type, fn, context = null) {
    super.once(type, fn, context = null);
    if (this._proxy) {
      this._proxy.once(type, event => {
        this.fire(type, event);
      }, this);
    }
    return this;
  }
  /**
   * 移出事件监听
   * 
   * @param {String} type 事件类型
   * @param {Function} handler 处理器
   * @param {Object} [context=null] 上下文
   * @return {EventDispatcher} 
   * 
   * @memberOf EventDispatcher#
   */
  off(type, fn, context = null) {
    super.off(type, fn, context);
    if (this._proxy) this._proxy.off(type, fn, context);
    return this;
  }
  /**
   * Alias to fire
   * 
   * @param {String} type 事件类型
   * @param {Object} data 需要传递的数据
   * @param {protected} name 是否传递给父级
   * @memberOf EventDispatcher#
   */
  dispatchEvent(type, data, propagate) {
    return this.dispatch(type, data, propagate);
  }
  /**
   * Alias to on
   * 
   * @param {String} type 事件类型
   * @param {Function} handler 处理器
   * @param {Object} [context=null] 上下文
   * @param {Boolean} [propagate=false] 是否传递到父级
   * @return {EventDispatcher}
   * @memberOf EventDispatcher#
   */
  addEventListener(type, handler, context = null, propagate = false) {
    this.on(type, handler, context = null, propagate = false);
    return this;
  }
  /**
   * Alias to off
   * 
   * @param {String} type 事件类型
   * @param {Function} handler 处理器
   * @param {Object} [context=null] 上下文
   * @return {EventDispatcher} 
   * 
   * @memberOf EventDispatcher#
   */
  removeEventListener(type, fn, context = null) {
    this.off(type, fn, context = null);
    return this;
  }
}


