import { Rectangle as LRectangle } from 'leaflet';
import AbstractPoly from './AbstractPoly';
import * as utils from '../../utils';
import { mapDataModel } from '../../models';

/**
 * 矩形
 * 
 * @export
 * @class Rectangle
 * @extends {AbstractPoly}
 */
export default class Rectangle extends AbstractPoly {
  constructor(pos, options = {}) {
    super(pos, options);
  }
  /**
   * 创建矩形
   * 
   * @protected 
   * @memberOf Rectangle#
   */
  createGraphics() {
    const latlng = utils.upprojectList(this.pos, mapDataModel.CRS); 
    return new LRectangle(latlng, this.options);
  }
}

