/**
 * @private 
 * @module  models/mapDataModel
 */

/**
 * 这个所谓的中心点, 不是指地图中心的意思. 其实是一个参照点, 地图数据里需要寻找任意一个参考点, 然后每个坐标都减去此参考点
 * 只不过其他两端都这么叫, 所以统一都这么称呼而已.
 * 
 * 存在这里的意义是, 提供全局的访问和存储, 据说不同的楼层只能用一个中心点
 */
let mapCenterPoint = null;
/**
 * 做楼层级别的数据缓存
 */
const mapDataCache = {};

let
  /**
   * 广场ID, 每次加载过来数据都会覆盖一次
   */ 
  plazaID = '',
  /**
   * 可能需要获取这个ID, 和上面一样, 每次都会获取一次
   */
  buildingID = '',

  map = null,
  /**
   * 存在这里的原因是, 有些类是获取不到map实例的, 那么全局存储一次, 能引用到Map的使用map, 引用不到的用此处的
   */
  crs = null;



// function getMapData(plazaID, floor) {
//   const key = `${plazaID}-${floor}`;
//   return mapDataCache[key];
// }
// function setMapData(plazaID, floor, data) {
//   const key = `${plazaID}-${floor}`;
//   mapDataCache[key] = data;
// }

// export default {
//   mapCenterPoint,
//   getMapData,
//   setMapData,
//   plazaID,
//   buildingID,
//   crs
// }


class MapDataModel {
  get map() {
    return map;
  }
  set map(leafletMap) {
    map = leafletMap;
  }
  get CRS() {
    return map.options.crs;
  }
  get plazaID() {
    return plazaID
  }
  set plazaID(id) {
    plazaID = id
  }
  get buildingID() {
    return buildingID;
  }
  set buildingID(id) {
    buildingID = id;
  }
  get mapCenterPoint() {
    return mapCenterPoint;
  }
  set mapCenterPoint(point) {
    mapCenterPoint = point;
  }
  getMapData(plazaID, floor) {
    const key = `${plazaID}-${floor}`;
    return mapDataCache[key];
  }
  setMapData(plazaID, floor, data) {
    const key = `${plazaID}-${floor}`;
    mapDataCache[key] = data;
  }
}

export default new MapDataModel();


