import Marker from './Marker';
import { DomUtil } from 'leaflet';


/**
 * 作为地图公共设施的标绘来使用
 * 
 * @private 
 * @export
 * @class POIMarker
 * @extends {Marker}
 */
export default class POIMarker extends Marker {
  constructor(pos, options) {
    super(pos, options);
    this._relativeLayer = options.relativeLayer;
    this._zoomLevelCache
  }
  /**
   * 系统的钩子
   * 
   * @memberOf POIMarker#
   */
  // getEvents() {
  //   return {
  //     zoom: this._zoomHandler
  //   };
  // }
  /**
   * 地图缩放的处理器, 主要是判断region的大小, 是否还显示此marker
   * 
   * @private 
   * @param {LeafletEvent} event 
   * 
   * @memberOf POIMarker#
   */
  _zoomHandler(event) {
    const image = this._marker.getElement();
    if (this._canshow()) {
      DomUtil.removeClass(image, 'zoom-hide');
    } else {
      DomUtil.addClass(image, 'zoom-hide');
    }
  }
  _getRegionSize() {
    if (!this._relativeLayer || !this._map) return {width: 0, height: 0};
    const bounds = this._relativeLayer.getBounds();
    const
      sw = this._map.latLngToLayerPoint(bounds.getSouthWest()),
      se = this._map.latLngToLayerPoint(bounds.getSouthEast()),
      ne = this._map.latLngToLayerPoint(bounds.getNorthEast()),
      nw = this._map.latLngToLayerPoint(bounds.getNorthWest());
    const width = Math.max(ne.x, se.x) - Math.max(nw.x, sw.x);
    const height = Math.max(sw.y, se.y) - Math.max(nw.y, ne.y);
    return { width, height };
  }
  _canshow() {
    const size = (this._marker && this._marker.options.icon && this._marker.options.icon.options.iconSize) || [16, 16];
    const regionSize = this._getRegionSize();
    return regionSize.width - size[0] > 1;
  }
}


