import Control from '../Control';
import template from './tpl.html';
import { DomUtil, DomEvent } from 'leaflet';
import { drawService } from '../../services';

'use strict';

/**
 * 绘制控制
 *
 * @private 
 * @export
 * @class DrawControl
 * @extends {Control}
 */
export default class DrawControl extends Control {
  constructor() {
    super('topleft');
  }
  /**
   * 添加之前, 系统的钩子
   * 
   * 
   * @memberOf DrawControl#
   */
  onAdd(map) {
    this._container = DomUtil.create('div', 'indoor-control-draw');
    this._container.innerHTML = template;
    this._addEvent(this._container);
    return this._container;
  }
  _addEvent(container) {
    DomEvent.on(container, 'click', event => {
      DomEvent.stop(event);
      DomEvent.stopPropagation(event);
      const target = event.target;
      const type = target.dataset.type;
      switch (type) {
        case 'polyline' :
          drawService.startPolyline();
          break;
        case 'polygon' :
          drawService.startPolygon();
          break;
        default:
      }
    });
  }
}


