/**
 * 渲染地图的超类, 理论上这里应该实现一个IRender的接口
 */

'use strict';
/**
 * 渲染器的超类
 * 
 * @private 
 * @abstract 
 * @export
 * @class AbstractRender
 */
export default class AbstractRender {
  /**
   * 初始化, 主要是提供map给render使用
   *
   * @param {leafletMap} map
   * @memberof AbstractRender#
   */
  initialize(map) {
    this._map = map;
    this._layerList = [];
  }
  /**
   * 渲染某个地图层, 注意是地图层, 有可能是SVG的, 也有可能是位图的
   *
   * @abstract 
   * @param {Object|Array|Promise} data 地图数据
   * @param {Object} option 可选参数
   * @memberof AbstractRender#
   */
  render(data, option) {
    throw new Error("IllegalOperationError");
  }
}



