/**
 * 创建集合
 * 
 * @private 
 * @module ui/marker/creator
 */
import L, {Util} from 'leaflet';
import { mapDataUtils } from '../../utils';

const defaultIconOption = {
  iconUrl: 'http://timg.ffan.com/convert/resize/url_T1qILTBvW_1RCvBVdK/tfs/e2427c1d08fc08691d6642f397fd1104.png',
  iconSize: [24, 24],
  iconAnchor: [12, 24],
  popupAnchor: [0, -24]
};

export function createMarker(pos, option) {
  option = option || {};
  const latlng = mapDataUtils.getLatlng(pos);
  if (!latlng) return null;
  const icon = createIcon((option && option.icon) || {});
  option.icon = icon;
  return L.marker(latlng, option);
}

export function createIcon(option) {
  option = option || {};
  const iconOption = Object.assign({}, defaultIconOption, option);
  return L.icon(iconOption);
}



