/**
 * 主要是考虑到后期会和服务端有交互, 服务端需要的是原始坐标, 而地图使用的是本地坐标, 提供一个全局转换的方法
 * 
 * @module Utils
 */

import L, { Point, Util, LatLng } from 'leaflet';
import { mapDataModel } from '../models';

/**
 * 将地图数据转换成原始数据
 * 
 * @param {LatLng} latlng
 */
function toOriginalCoordinates(latlng) {
  if (mapDataModel.mapCenterPoint) {
    if (Array.isArray(latlng)) {
      latlng = L.latLng(latlng);
    }
    let point = mapDataModel.CRS.projection.project(latlng);
    point.x += Number(mapDataModel.mapCenterPoint[0]);
    point.y += Number(mapDataModel.mapCenterPoint[1]);
    return point;
  } else {
    return Point(0, 0);
  }
}
/**
 * 地图坐标转屏幕坐标
 * 
 * @param {Latlng} latlng
 * 
 * @return {Point}
 */
function latLngToPoint(latlng) {
  return mapDataModel.CRS.projection.project(latlng);
}
/**
 * 屏幕坐标转地图坐标
 * 
 * @param {Point} point
 * 
 * @return {LatLng}
 */
function pointToLatLng(point) {
  if (Util.isArray(point)) {
    return mapDataModel.CRS.projection.unproject(new Point(point[0], point[1]));
  }
  if (point instanceof Point) {
    mapDataModel.CRS.projection.unproject(point);
  }
}

/**
 * 把坐标数组, 转化成latlng数组. 实际上是把每一组坐标都映射成地图坐标
 * 
 * @private 
 * 
 * @param {Array} list 
 * @param {Function} unproject 
 * 
 * @return {Array} 转换后的数组
 */
function upprojectList (list, crs) {
  const latlngList = [];
  if (list && list.length > 0) {
    list.forEach(pos => {
      const latlng = transformCoordinates(pos, crs);
      latlngList.push([latlng.lat, latlng.lng]);
    });
  }
  return latlngList;
}
/**
 * 兼容一下两种数据格式, 有可能直接传坐标, 也有可能传的就是经纬度
 * 
 * @private
 * 
 * @param {Array|LatLng} pos 
 * @param {CRS} crs 
 * @return {LatLng}
 */
function transformCoordinates(pos, crs) {
  if (pos instanceof L.LatLng) {
    return pos;
  }
  if (Util.isArray(pos)) {
    return crs.unproject(new Point(pos[0], pos[1]));
  }
  return L.latlng(pos);
}

function getOriginalByLatlngs(latlngList) {
  let coords = [];
  if (!latlngList) return coords;
  for (let i = 0, coord; i < latlngList.length; ++i) {
    const child = latlngList[i];
    if (L.Util.isArray(child)) {
      coord = this.getOriginalByLatlngs(child);
    } else {
      const point = this.toOriginalCoordinates(child);
      coord = [point.x, point.y]
    }
    coords.push(coord);
  }
  return coords;
}

export {
  toOriginalCoordinates,
  getOriginalByLatlngs,
  latLngToPoint,
  pointToLatLng,
  upprojectList
};


