import L, {LatLng, Point, FeatureGroup } from 'leaflet';
import GlobalConfig from '../configs';
import { mapDataModel } from '../models';


function create(id, options) {
  // 修改默认配置
  const mapOption = Object.assign({}, GlobalConfig.map, options);
  mapOption.editOptions = GlobalConfig.draw;
  // 实例化地图
  const map = new L.Map(id, mapOption);
  // 解决裁剪问题
  map.getRenderer(map).options.padding = 100;
  // 保存到全局可访问点
  mapDataModel.map = map;
  //
  return map;
}


export default {
  create
}


