import Layer from '../Layer';
import * as utils from '../../utils';

const DEFAULT_OPTIONS = {
  pane: 'poly'
};

export default class AbstractPoly extends Layer {
  constructor(pos, options = {}) {
    super(options);
    /**
     * 描述各个点坐标
     * 
     * @protected 
     * @type {Array.<Array>}
     */
    this.pos = pos;
    /**
     * 具体创建的选项
     * 
     * @protected 
     * @type {Object}
     */
    this.options = Object.assign({}, DEFAULT_OPTIONS, options);
    /**
     * 图形, polyline polygon circle ....
     * 
     * @protected 
     * @type {Leaflet.Polypath}
     */
    this.graphics = this.createGraphics(map);
    this.layerDisplay = this.graphics;
    this.injectDispatcher(this.graphics);
  }
  /**
   * 引擎的钩子
   * 
   * @protected 
   * @param {LeafletMap} map 
   * 
   * @memberOf AbstractPoly#
   */
  onAdd(map) {
    this.graphics.addTo(map);
  }
  /**
   * 引擎的钩子
   * 
   * @protected 
   * @param {LeafletMap} map 
   * 
   * @memberOf AbstractPoly#
   */
  onRemove(map) {
    if (this.graphics) {
      map.removeLayer(this.graphics);
    }
  }
  /**
   * 打开编辑功能
   * 
   * @param {LeatletMap} [map=null] 
   * @return {Object}
   * 
   * @memberOf AbstractPoly#
   */
  enableEdit(map = null) {
    return this.graphics.enableEdit(map);
  }
  /**
   * 关闭编辑功能
   * 
   * 
   * @memberOf AbstractPoly#
   */
  disableEdit() {
    this.graphics.disableEdit();
  }
  /**
   * 切换编辑状态
   * 
   * 
   * @memberOf AbstractPoly#
   */
  toggleEdit() {
    this.graphics.toggleEdit();
  }
  /**
   * 检测是否可编辑
   * 
   * @return {Boolean} 
   * 
   * @memberOf AbstractPoly#
   */
  editEnabled() {
    return this.graphics.editEnabled();
  }
  /**
   * 创建图形, 需要子类来实现此方法, 以创建确定的某个图形实例
   * 
   * @protected 
   * @param {any} map 
   * @return {Leaflet.Polypath}
   * 
   * @memberof AbstractPoly#
   */
  createGraphics(map) {
    throw new Error('IllegalOperationError in AbstractPoly');
  }
  /**
   * 获取坐标组
   * 
   * @override 
   * @return {LatLng}
   * 
   * @memberOf AbstractPoly#
   */
  getLatLngs() {
    return this.graphics.getLatLngs();
  }
  /**
   * 获取源坐标组
   * 
   * @override 
   * @return {Array.<Array>}
   * 
   * @memberOf AbstractPoly
   */
  getOriginalLatlng() {
    return utils.getOriginalByLatlngs(this.getLatLngs());
  }
  /**
   * 设置坐标
   * 
   * @override 
   * @param {Array.<Array|LatLng>} latlngs 
   * 
   * @memberOf AbstractPoly#
   */
  setLatLngs(latlngs) {
    latlngs = utils.upprojectList(latlngs);
    this.graphics.setLatLngs(latlngs);
  }
  /**
   * 设置样式
   * 
   * @param {Object} options 
   * 
   * @memberOf AbstractPoly#
   */
  setStyle(options) {
    this.graphics.setStyle(options);
  }
  /**
   * 返回路径的LatLngBounds对象
   * 
   * @returns LatLngBounds
   * 
   * @memberOf AbstractPoly#
   */
  getBounds() {
    return this.graphics.getBounds();
  }
}


