import { Polyline as LPolyline, CRS } from 'leaflet';
import AbstractPoly from './AbstractPoly';
import * as utils from '../../utils';
import { mapDataModel } from '../../models';

/**
 * 基于SVG的线
 * 
 * @export
 * @class Polyline
 * @extends {Layer}
 * 
 * @param {Array.<Array>} pos 描述各个点坐标
 * @param {Object} options 选项
 */
export default class Polyline extends AbstractPoly {
  constructor(pos, options = {}) {
    super(pos, options);
  }
  /**
   * 创建线
   * 
   * @param {Leaflet.Map} map 
   * @return {Leaflet.Polyline}
   * 
   * @memberof Polyline
   */
  createGraphics(map) {
    const latlngs = utils.upprojectList(this.pos, mapDataModel.CRS);
    return new LPolyline(latlngs, this.options);
  }
}


