import L, { DomUtil, Point } from 'leaflet';
import { EventDispatcher } from '../events';
import { mapDataModel } from '../models';
import { toOriginalCoordinates, toScreenCoordinates } from '../utils';
import popupAction from './popup/popupAction';

/**
 * Layer作为系统内部UI的超类, 换句话说, 里面的 **display** 都是基于leaflet的Layer
 * Layer的作用仅仅在于代理内部方法, 以及提供相同的类型和操作使用
 * 
 * @export
 * @class Layer
 * @extends {EventDispatcher}
 */
export default class Layer extends EventDispatcher{
  constructor(options) {
    super();
    /**
     * 要代理的UI, 实际上是继承了*Leaflet.Layer*的或是实现了关键接口的实例
     * 
     * @protected 
     * @member {LeafletLayer|ILeafletLayer} layerDisplay
     * @property {Layer|ILayer} layerDisplay
     * @memberof Layer#
     */
    this.layerDisplay = null;
    /**
     * 配置项
     * 
     * @property {Object} options 配置项
     * @memberof Layer#
     */
    this.options = options;
  }
  /**
   * 针对框架Layer实现的方法, 主要是用做代理让自定义类也能无缝添加
   * 
   * @param {LeafletMap} name leaflet map instance
   * 
   * @memberof Layer#
   */
  addTo(map) {
    map.addLayer(this);
    return this;
  }
  /**
   * 从地图移出此层
   * 
   * @return {Layer} 返回被移出的层
   * 
   * @memberof Layer#
   */
  remove() {
    this._map.removeLayer(this);
    return this;
  }
  /**
   *  虽然说是钩子, 仔细看一下框架不仅会主动调用这个方法
   * 
   * @param {leafletMap} map 
   * 
   * @memberof Layer#
   */
  onAdd(map) {
    if (this.layerDisplay) {
      this.getPane(map).appendChild(this.layerDisplay);
    }
  }
  /**
   * 同上, 如何移出也需要在此方法里实现
   * 
   * @param {leafletMap} map 
   * @memberof Layer#
   */
  onRemove(map) {
    if (this.layerDisplay) {
      DomUtil.remove(this.layerDisplay);
    }
  }
  /**
   * 获取此层属于哪个层级, 如果选项中没有定义 *pane*, 或获取的层不存在, 那么将返回 overlayPane
   * 
   * @protected 
   * @param {LeafletMap} map
   * 
   * @memberof Layer#
   */
  getPane(map) {
    const paneName = (this.options && this.options.pane) ? this.options.pane : 'overlayPane';
    let pane = map.getPane(paneName);
    if (!pane) {
      pane = map.getPane('overlayPane');
    }
    return pane;
  }
  /**
   * 获得地图坐标
   * 
   * @return {Array|Object}
   * 
   * @memberOf Layer#
   */
  getLatLngs() {
    if (this.layerDisplay) {
      return this.layerDisplay.getLatLng();
    }
  }
  /**
   * 获取原始坐标, 也就是和中心点无关的坐标#
   * 
   * @memberOf Layer#
   */
  getOriginalLatlng() {
    if (this.layerDisplay) {
      return toOriginalCoordinates(this.layerDisplay.getLatLng());
    }
  }
  getScreenPoint() {
    if (this.layerDisplay) {
      return toScreenCoordinates(this.layerDisplay.getLatLng());
    }
  }
  /**
   * 设置地图坐标
   * 
   * @param {LatLng} latlng 
   * 
   * @memberOf Layer#
   */
  setLatLngs(latlng) {
    if (this.layerDisplay) {
      this.layerDisplay.setLatLng(latlng);
    }
  }
  /**
   * 获取显示元素
   * 
   * @returns HTMLElement
   * 
   * @memberOf Layer#
   */
  getElement() {
    return this.layerDisplay;
  }
  /**
   * 这是原来框架的方法的拷贝, 在系统里会强制调用此方法
   * 
   * @private
   * @param {any} e 
   * @memberof Layer#
   */
  _layerAdd(e) {
    var map = e.target;

    // check in case layer gets added and then removed before the map is ready
    if (!map.hasLayer(this)) { return; }

    this._map = map;
    this._zoomAnimated = map._zoomAnimated;

    if (this.getEvents) {
      var events = this.getEvents();
      map.on(events, this);
      this.once('remove', function () {
        map.off(events, this);
      }, this);
    }

    this.onAdd(map);

    // if (this.getAttribution && map.attributionControl) {
    //   map.attributionControl.addAttribution(this.getAttribution());
    // }

    this.fire('add');
    map.fire('layeradd', {layer: this});
  }
  /**
   * 绑定Popup, 字符串是直接当HTML使用, Object是走内部模板
   * 
   * @param {String|Object} content 
   * @param {Object} [options={}] 
   * @return {Layer}
   * 
   * @memberOf Layer#
   */
  bindPopup(content, options = {}) {
    popupAction.bindPopup(content, options, this);
    return this;
  }
  /**
   * 解除绑定
   * 
   * @return {Layer}
   * 
   * @memberOf Layer#
   */
  unbindPopup() {
    popupAction.unbindPopup(this);
    return this;
  }
  /**
   * 用API打开某个绑定过的Popup
   * 
   * @param {Latlng} latlng 
   * @return {Layer} 
   * 
   * @memberOf Layer#
   */
  openPopup(latlng = null) {
    popupAction.openPopup(this, latlng);
    return this;
  }
  /**
   * 用API关闭Popup
   * 
   * 
   * @memberOf Layer#
   */
  closePopup() {
    popupAction.closePopup(this);
  }
  /**
   * open or close Popup
   * 
   * 
   * @memberOf Layer#
   */
  togglePopup() {
    popupAction.togglePopup(this);
  }
  /**
   * 用API来修改内容
   * 
   * @param {String|Object} content 
   * 
   * @memberOf Layer#
   */
  setPopupContent(content) {
    popupAction.setPopupContent(this, content);
  }
  /**
   * 确实某个绑定在Layer上的Popup是否打开
   * 
   * @return {Boolean}
   * 
   * @memberOf Layer
   */
  isPopupOpen() {
    return popupAction.isPopupOpen(this);
  }
  /**
   * 绑定Tooltip
   * 
   * @param {String|Object|HTMLElement} content 
   * @param {Object} [options={}] 
   * @return {Layer}
   * 
   * @memberOf Layer#
   */
  bindTooltip(content, options = {}) {
    options.offset = options.offset || (new Point(-5, -15));
    this.layerDisplay.bindTooltip(content, options);
    
    return this;
  }
  /**
   * 解除对tooltip的绑定
   * 
   * @return {Layer}
   * 
   * @memberOf Layer#
   */
  unbindTooltip() {
    if(this.layerDisplay._tooltip) {
      this.layerDisplay.closeTooltip();
      this.layerDisplay._tooltip = null;
    }
    return this;
  }
}


